import Image from "next/image";
import Badge from "@/src/components/base/Badge";
import { Download } from "lucide-react";

type DownloadCardProps = {
  title: string;
  description?: string;
  fileType?: "PDF" | "ZIP" | "DOC" | "PPT";
  fileSize?: string;
  driveFileId: string;
  thumbnail?: string;
};

const DownloadCard = ({
  title,
  description,
  fileType = "PDF",
  fileSize,
  driveFileId,
  thumbnail,
}: DownloadCardProps) => {
  const downloadUrl = `https://drive.google.com/uc?export=download&id=${driveFileId}`;

  return (
    <div className="rounded-2xl border border-black/10 bg-white shadow-md hover:shadow-xl transition p-4 flex flex-col justify-between">
      
      {/* Thumbnail */}
      {thumbnail && (
        <div className="relative h-36 w-full mb-4 rounded-xl overflow-hidden">
          <Image
            src={thumbnail}
            alt={title}
            fill
            className="object-cover"
          />
        </div>
      )}

      {/* Content */}
      <div className="flex flex-col gap-2 flex-1">
        <div className="flex items-center justify-between">
          <h3 className="font-semibold text-gray-800 text-base">
            {title}
          </h3>
          <Badge>{fileType}</Badge>
        </div>

        {description && (
          <p className="text-sm text-gray-600 line-clamp-2">
            {description}
          </p>
        )}

        {fileSize && (
          <p className="text-xs text-gray-500">
            File size: {fileSize}
          </p>
        )}
      </div>

      {/* Download Button (Direct Download) */}
      <a
        href={downloadUrl}
        download
        className="mt-4 inline-flex items-center justify-center gap-2 rounded-xl bg-black text-white py-2 text-sm font-medium hover:bg-black/90 transition"
      >
        <Download size={16} />
        Download
      </a>
    </div>
  );
};

export default DownloadCard;
